.movies_movie {
	display: flex;
	flex-direction: row;
	margin: 50px;
	width: 550px;
	height: 250px;

	background-color: #eff3f7;
	box-shadow: 5px 3px rgba(0, 0, 0, 0.1);
}

.movies_img {
	display: inline-block;
	margin-right: 50px;
	z-index: 2;
}

.movie_img {
	display: inline-block;
	max-width: 170px;
	height: auto;

	position: relative;
	top: -30px;
	left: 30px;
}

.movies_data {
	display: flex;
	flex-direction: column;

	padding-top: 10px;
	padding-right: 20px;
}

.movies_main {
	height: 120px;
}

.movie_title {
	display: inline-block;
	margin: 0 0 5px 0;
}

.movie_year {
	margin: 0 0 5px 0;
	opacity: 0.5;
}

.genres {
	margin: 0;
	padding: 0 10px 0 0;
	height: 42px;
}

.genres li {
	display: inline-block;
	margin-right: 6px;

	opacity: 0.3;
}

.movies_summary {
	margin: 0 0 0 0;
	width: 350px;
	height: 83px;

	position: relative;
	left: -50px;
	background-color: rgba(255, 0, 0, 0.1);

	overflow: scroll;
}

.movie_summary {
	margin: 0 0 0 50px;
	padding: 10px 15px;
}
